<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <span class="text-h5">Exportar/Importar Visualizações</span>
      </v-col>
      <v-col cols="12" class="py-0 font-weight-light">
        <span>
          Exportar todas as visualizações criadas no BI Ripper para um arquivo <code>.json</code>
        </span>
      </v-col>
      <v-col cols="4">
        <v-btn
          block
          elevation="0"
          color="amber"
          class="white--text"
          @click="exportViewsToJson"
        >Exportar</v-btn>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12">
        <v-divider></v-divider>
      </v-col>
    </v-row>
    
    <v-row>
      <v-col cols="12">
        <span class="text-h5">Importar Visualizações</span>
      </v-col>
      <v-col cols="12" class="py-0 font-weight-light">
        <span>
          Substitui todas as visualizações criadas no BI Ripper pelas visualizações contidas no arquivo <code>.json</code> importado.
        </span>
      </v-col>
      <v-col cols="6">
        <v-file-input
          v-model="selectJsonFile"
          label="Selecione o arquivo '.json'"
          accept="application/json"
          outlined
          dense
          show-size
          truncate-length="35"
        ></v-file-input>
      </v-col>
      <v-col cols="4">
        <v-btn
          block
          elevation="0"
          color="red"
          class="white--text"
          @click="importViewsFromJson"
        >Importar</v-btn>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12">
        <v-divider></v-divider>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12">
        <router-link to="/version-info">BI Ripper - Informações da versão</router-link>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import viewManager from "../services/view-manager";

export default {
  name: 'Ajustes',
  title: 'Ajustes',

  data: () => ({
    selectJsonFile: null
  }),

  methods: {
    exportViewsToJson: function() {
      console.log('export')
      viewManager.exportJsonStoredViews()
      this.showToast('Visualizações exportadas com sucesso!', 'Verifique a janela de download que se abriu.')
    },
    importViewsFromJson: async function() {
      if (!this.selectJsonFile) {
        this.showToast('Selecione um arquivo para importação', '')
        return
      }

      try {
        const storedViews = await viewManager.importViews(this.selectJsonFile)
        this.selectJsonFile = null
        this.showToast('Visualizações importadas com sucesso!', Object.keys(storedViews).join(', '))
      } catch (error) {
        console.error(error)
      }
    },
    showToast: function(title, message) {
      this.$swal({
        showConfirmButton: true,
        allowEscapeKey: true,
        allowEnterKey: true,
        allowOutsideClick: false,
        title: title,
        text: message,
        icon: 'info',
      });
    }
  }
}
</script>
